@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css?family=Exo:100);
@keyframes bg-scrolling-reverse{100%{background-position:50px 50px}}@keyframes bg-scrolling{0%{background-position:50px 50px}}body{margin-top:13.5rem;color:#999;font:400 16px/1.5 exo, ubuntu, "segoe ui", helvetica, arial, sans-serif;text-align:center;background:url("data:image/png;base64,") repeat 0 0;animation:bg-scrolling-reverse .92s infinite;animation-timing-function:linear}body::before{content:"CRYPTOCURRENCY";font-size:8rem;font-weight:100;font-style:normal}.coin-app{display:flex;flex-direction:column;align-items:center;margin-top:64px;color:#000}.coin-input{padding-left:16px;width:300px;height:50px;border-radius:4px;border:none;background-image:linear-gradient(-225deg, #ac32e4 0%, #7918f2 48%, #4801ff 100%);color:black}.coin-input::-webkit-input-placeholder{color:#e2e2e2}.coin-input:-ms-input-placeholder{color:#e2e2e2}.coin-input::placeholder{color:#e2e2e2}

.coin-container{display:flex;justify-content:center}.coin-row{display:flex;flex-direction:row;justify-content:start;align-items:center;height:80px;border-bottom:1px solid #080808;width:900px}.coin{display:flex;align-items:center;padding-right:24px;min-width:300px}.coin h1{font-size:16px;width:150px}.coin img{height:30px;width:30px;margin-right:10px}.coin-symbol{text-transform:uppercase;font-weight:bold}.coin-data{display:flex;text-align:right;justify-content:space-between;width:100%;font-weight:bold}.coin-price{width:110px;font-weight:bold}.coin-volume{width:155px;font-weight:bold}.coin-marketcap{width:230px;font-weight:bold}.coin-percent{width:100px;font-weight:bold}.red{color:#f00606}.green{color:#11d811}

